<template>
  <AppBorder :app-info="Crontab" :size="size" herder-css="header1">
    <template v-slot:header>
      <div class="fx-ac">
        <span>{{ Crontab.icon }}定时任务</span>
      </div>
    </template>
    <template v-slot:main>
      <div class="fx-ver CrontabBox" @mousemove="(ev)=>{ev.stopPropagation()}">
        <div class="fx-pj title">
          <span></span>
          <button class="fz26" @click="taskList">任务列表</button>
          <button class="fz26" @click="curPage=2">设置任务</button>
          <button class="fz26" @click="curPage=3">定时消息</button>
          <span></span>
        </div>
        <div v-show="curPage===1">
          <el-table
              :data="tableData"
              max-height="600"
              border
              style="width: 100%">
            <el-table-column
                v-for="(col,idx) of tableCol"
                :key=idx
                :prop=col.prop
                :label=col.label
                :width=col.width>
            </el-table-column>
            <el-table-column
                prop=""
                label="操作"
                width="200">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="cancel(scope.row.id)">关闭任务</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div v-show="curPage===2">
          <el-form :model="ruleForm" status-icon :rules="rules" ref="schedule" label-width="120px"
                   class="demo-ruleForm fx-ver fx-pac">
            <el-form-item :label="val" :prop="key" v-for="(val,key,idx) in formItem" :key="idx">
              <el-input type="text" v-model="ruleForm[key]" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item class="">
              <el-button type="primary" @click="submitForm('schedule')">提交</el-button>
              <el-button @click="resetForm('schedule')">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
        <div v-show="curPage===3" class="fx-ver fx-pac">
          <el-form :model="notifyForm" status-icon :rules="rules2" ref="notify" label-width="120px"
                   class="demo-ruleForm fx-ver fx-pac">
            <el-form-item :label="val" :prop="key" v-for="(val,key,idx) in formItem2" :key="idx">
              <el-input type="text" v-model="notifyForm[key]" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item class="">
              <el-button type="primary" @click="submitForm('notify')">提交</el-button>
              <el-button @click="resetForm('notify')">重置</el-button>
            </el-form-item>
          </el-form>
          <button @click="gotoHelper">->前往Cron在线表达式生成器</button>
        </div>
      </div>
    </template>
  </AppBorder>
</template>

<script>
import AppBorder from '../AppBorder'
import pageManager from "@/utils/PageManager";

export default {
  name: "Crontab",
  components: {AppBorder},
  data() {
    return {
      Crontab: {
        name: "Crontab",
        isLoad: false,
        min: true,
        icon: '⏱️'
      },
      size: {
        width: '1200px',
        height: '600px'
      },
      ruleForm: {
        serviceName: '',
        methodName: '',
        cron: '',
        paramsName: '',
        params: ''
      },
      notifyForm: {
        qq: '',
        email: '',
        cron: '',
        content: ''
      },
      formItem: {
        serviceName: '服务名',
        methodName: '方法名',
        cron: 'Cron表达式',
        paramsName: '参数全类名列表',
        params: '参数值'
      },
      formItem2: {
        qq: 'qq号',
        email: '邮箱',
        cron: 'Cron表达式',
        content: '消息内容'
      },
      rules: {
        serviceName: [
          {required: true, message: '请输入调用服务名', trigger: 'blur'}
        ],
        methodName: [
          {required: true, message: '请输入调用方法名', trigger: 'blur'}
        ],
        cron: [
          {required: true, message: '请输入Cron表达式', trigger: 'blur'}
        ]
      },
      rules2: {
        qq: [
          {required: true, message: '请输入qq号', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '请输入邮箱', trigger: 'blur'}
        ],
        cron: [
          {required: true, message: '请输入Cron表达式', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '请输入消息内容', trigger: 'blur'}
        ]
      },
      tableData: [],
      curPage: 1,
      tableCol: [
        {prop: "id", label: "id", width: 180},
        {prop: "serviceName", label: "服务名", width: 180},
        {prop: "methodName", label: "方法名", width: 120},
        {prop: "params", label: "参数", width: 150},
        {prop: "cron", label: "Corn", width: 120},
        {prop: "runCount", label: "已执行次数", width: 70},
      ]
    }
  },
  created() {
    this.getTaskList();
  },
  methods: {
    getTaskList() {
      this.$http.get("/api/getTaskList")
          .then(ret => {
            if (ret.code === 200) {
              ret.data.forEach(v => {
                v.params && (v.params = v.params.join(","));
                v.paramsName && (v.paramsName = v.paramsName.join(","));
              })
              this.tableData = ret.data;
            } else
              this.$message.error(ret.msg)
          })
    },
    cancel(id) {
      this.$http.get("/api/cancelTask", {id})
          .then(ret => {
            if (ret.code === 200) {
              this.$message.success(ret.data);
              this.getTaskList();
            } else
              this.$message.error(ret.msg)
          }).catch(err => {
        this.$message.error(err);
      })
    },
    taskList() {
      this.curPage = 1;
      this.getTaskList();
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    setSchedule() {
      let paramsName = this.ruleForm.paramsName.split(',');
      let params = this.ruleForm.params.split(",");
      let param = {};
      if (paramsName[0] === '' || params[0] === '') {
        param.serviceName = this.ruleForm.serviceName;
        param.methodName = this.ruleForm.methodName;
        param.cron = this.ruleForm.cron;
      } else param = {...this.ruleForm, paramsName, params}
      this.$http.postBody('/api/setSchedule', param
      ).then(ret => {
        if (ret.code === 200) {
          console.log(ret)
          this.$message.success("任务配置成功" + ret.data);
        } else
          this.$message.error(ret.msg)
      }).catch(err => {
        this.$message.error(err);
      });
    },
    setNotify(){
      this.$http.postBody('/api/notifyByTwoWay', this.notifyForm
      ).then(ret => {
        if (ret.code === 200) {
          console.log(ret)
          this.$message.success("任务配置成功" + ret.data);
        } else
          this.$message.error(ret.msg)
      }).catch(err => {
        this.$message.error(err);
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if ('schedule' === formName) this.setSchedule();
          if ('notify'=== formName) this.setNotify();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    gotoHelper(){
      pageManager.search("http://cron.ciding.cc/");
    },
    closeApp() {
      this.$router.push("/desktop");
    }
  }
}
</script>

<style scoped>
.title {
  margin: 20px 0;
}

.CrontabBox {
  background-color: white;
  width: 99.8%;
  height: 99%;
  border: 1px saddlebrown solid;
}
</style>
